.footer {
    max-width: 880px;
    padding: 66px 0 60px;
    margin: 0 auto;
}

@media screen and (min-width: 638px) and (max-width: 900px) {
    .footer {
        max-width: calc(100% - (27px * 2));
    }
}

@media screen and (min-width: 320px) and (max-width: 637px) {
    .footer {
        max-width: 282px;
        padding: 48px 0 36px;
    }
}